      text-align: center;
      margin: 0 10px 0 10px; }
  .FormInput-multiElementRow {
    height: 41px;
    display: grid;
    grid-auto-flow: column;
    grid-auto-columns: -webkit-max-content;
    grid-auto-columns: max-content;
    grid-gap: 4px;
    align-items: center; }
    .FormInput-multiElementRow--bigger {
      padding: 1px;
      height: calc(41px + 6px); }
  .FormInput-description {
    margin: 0 10px 0 10px;
    font-size: 0.8rem;
    align-items: center;
    display: grid; }
    .FormInput-description--bigGap {
