    color: #fff;
    background-color: #F6BBBC;
    font-size: 0.7rem;
    letter-spacing: 0.05rem;
    padding: 4px;
    align-self: center;
    justify-self: left;
    border-radius: 4px;
    position: relative; }
    .FormInput-error--arrow::before {
      z-index: -1;
      content: "";
      position: absolute;
      display: block;
      width: 0px;
      height: 0;
      border: 6px solid #F6BBBC;
      -webkit-transform: rotate(45deg);
              transform: rotate(45deg);
      left: -3px;
      bottom: calc(50% - 6px); }
  .FormInput--inlineError {
    display: grid;
    grid-gap: 5px;
    margin-left: 5px; }

@media (max-width: 750px) {
  .FormInput {
