@import "../../../style/variables";

.FormBlock {
  padding: 15px 45px 15px 45px;
  background-color: $form_background_color;
  display: grid;
  grid-gap: 15px;
  box-shadow: 0px 0px 4px 1px rgba(0, 0, 0, 0.1);
  &-title {
    color: $main_blue;
    font-size: 1.5rem;
    letter-spacing: -0.04rem;
    font-weight: 600;
    display: grid;
    grid-gap: 10px;
  }
  &-line {
    width: 100%;
    height: 2px;
    background-color: $main_border_color;
  }
  &--success {
    height: 100px;
    min-width: 250px;
    background-color: $success_color;
    margin: 25px 0 25px 0;
    & .FormBlock-title {
      color: $success_color-title;
      font-size: 1.3rem;
      font-weight: 500;
    }
    & .FormBlock-line {
      display: none;
    }
    & .FormBlock-description {
      font-size: 0.9rem;
      letter-spacing: -0.02rem;
    }
  }
}

@media (max-width: 750px) {
  .FormBlock {
    padding: 15px 25px 15px 25px;
    grid-gap: 10px;
    &-title {
      font-size: 1.3rem;
    }
  }
}

@media (max-width: 550px) {
  .FormBlock {
    padding: 10px 10px 10px 10px;
    grid-gap: 5px;
  }
}
